<template>
  <div class="toast-mask" @click="$emit('hide')">
    <div class="toast-box" @click.stop="noop">
      <div class="toast-title">
        <span class="gradient-title">{{$t('赎回')}}</span> 
      </div>
      <div class="toast-info">
        <div class="info">
          <span>{{$t('质押')}}{{record.mainTokenSympol}}{{$t('数量')}}</span>
          <span>{{record.mainAmount}}{{record.mainTokenSympol}}</span>
        </div>
        <div class="info" v-if="record.deputyTokenSympol">
          <span>{{$t('质押')}}{{record.deputyTokenSympol}}{{$t('数量')}}</span>
          <span>{{record.deputyAmount}}{{record.deputyTokenSympol}}</span>
        </div>
        <div class="info">
          <span>{{$t('质押获得的总算力')}}</span>
          <span>{{record.power}}</span>
        </div>
        <van-slider v-model="percent" :min="1" class="slider" active-color="#FFC000" />
        <div class="slider-tip">
          <div class="min-max">
            <span>1</span>
            <span>100</span>
          </div>
          <div class="current">
            <span>{{percent}}%</span>
          </div>
        </div>
      </div>
      <div @click="redeem" class="redeem-btn theme-btn">
        <span>{{$t('赎回')}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import investAbi from '@/utils/investAbi'
import config from '@/utils/config'
import { mapState } from 'vuex'
export default {
  props: {
    record: {
      type: Object,
      default: function() {
        return {
          id: 0,
          mainAmount: 0,
          deputyAmount: 0,
          mainTokenSympol: '--',
          deputyTokenSympol: '--',
          power: 0
        };
      }
    }
  },
  data() {
    return {
      percent: 0
    }
  },
  created () {
    if(this.account) {
       this.initContract() 
    }  
  },
  methods: {
     initContract () {
      this.investContract = new this.provider.eth.Contract(investAbi, config.investAddress);
    },
    noop () {},
    async redeem() {
      if(Number(this.percent) <=0) {
        Toast(this.$t('请选择赎回的百分比'))
        return
      }
      await this.investContract.methods.withdrawRecord(this.record.id, Number(this.percent * 100)).send({from: this.account})
      Toast(this.$t('赎回成功'))
      this.$emit('hide')
    }
  },
   computed: {
    ...mapState(['account', 'provider'])
  },
   watch: {
    account (val) {
      if(val) {
        this.initContract()
      }
    }
  }
}
</script>

<style scoped>
.toast-mask {
  position: fixed;
  z-index: 200;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.4);
}
.toast-mask .toast-box {
  position: absolute;
  background: #fff;
  width: 90%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 5px;
  padding-bottom: 30px;
}
.toast-mask .toast-box .toast-title {
  padding: 8px 12px;
  border-bottom: 1px solid #ddd;
}
.toast-mask .toast-box .toast-info {
  padding: 15px;
}
.toast-mask .toast-box .toast-info .info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 12px;
}
.toast-mask .toast-box .toast-info .slider {
  margin-top: 30px;
}
.toast-mask .toast-box .toast-info .slider-tip {
  padding-top: 10px;
}
.toast-mask .toast-box .toast-info .slider-tip .min-max {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.toast-mask .toast-box .toast-info .slider-tip .current {
  text-align: center;
  margin-top: 2px;
}
.toast-mask .toast-box .toast-tip {
  padding: 0 10px;
}
.toast-mask .toast-box .toast-tip .tip {
  font-size: 14px;
}
.toast-mask .toast-box .redeem-btn {
  margin-top: 20px;
  text-align: center
}

</style>
