<template>
  <div class="notice" ref="container">
    <showHeader></showHeader>
    <div class="noticeList" ref="inner">
      <div class="notice_item" v-for="(item, index) in list">
        <div class="item_title">
          {{ item.title }}
          <p>{{ item.create_time }}</p>
        </div>
        <div class="item_text" v-html="item.content"></div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import showHeader from "@/components/header.vue";
import axios from "axios";

export default {
  data() {
    return {
      list: [],
      pageSize: 1,
      containerHeight: 0,
      innerHeight: 0,
    };
  },
  components: {
    showHeader,
  },
  mounted() {
    this.getList();
    window.addEventListener("scroll", this.handleScroll, true);
  },
  methods: {
    getList() {
      axios
        .post("https://admin.yfi9.com/api/notice/getnoticelists", {
          lang: sessionStorage.getItem("languageSet"),
          page: this.pageSize,
          page_size: 15,
        })
        .then((res) => {
          this.list = this.list.concat(res.data.data.data);
          this.count = res.data.data.last_page;
        });
    },
    handleScroll() {
      let scrollTop =
        document.body.scrollTop || document.documentElement.scrollTop;
      if (scrollTop > 100) {
        if (this.pageSize < this.count) {
          this.pageSize += 1;
          this.getList();
        }
      }
    },
  },
};
</script>
<style scoped>
.noticeList {
  padding: 0.3rem;
  box-sizing: border-box;
}
.notice_item {
  background: #fff;
  padding: 0.3rem;
  margin-bottom: 0.2rem;
}
.item_title {
  border-bottom: 0.01rem solid #eee;
  color: #fabf18;
}
.item_title p {
  text-align: right;
  color: #999;
}
.item_text {
  margin-top: 0.3rem;
  line-height: 0.5rem;
}
</style>
