<template>
  <div class="share">
    <showHeader></showHeader>
    <div class="logo_box">
      <h2>{{ $t("分享") }}</h2>
      <img src="../assets/img/fenxiangye_logo@2x.png" alt="" />
    </div>
    <div class="qrCode">
      <img :src="qr" alt="" />
      <p>{{ $t("扫描或复制链接") }}</p>
      <a href="javascript:void(0)">{{ link }}</a>
      <div class="clip-button" :data-clipboard-text="link">
        {{ $t("单击复制地址") }}
      </div>
    </div>
    <div class="site">
      <span>{{account}}</span>
      <img src="../assets/img/fenxiangye_fuzhi@2x.png" :data-clipboard-text="account" alt="" />
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import showHeader from "@/components/header.vue";
import { mapState } from "vuex";
import Clipboard from "clipboard";
import QRCode from "qrcode";
import { Toast } from "vant";
export default {
  name: "share",
  data() {
    return {
      qr: "",
      link: "",
    };
  },
  components: {
    showHeader,
  },
  computed: {
    ...mapState(["account", "provider"]),
  },
  created() {
    if (this.account) {
      this.initContract();
    }
  },
  methods: {
    initContract() {
      this.link = `${window.location.origin}/#/?intro=${this.account}`;
      QRCode.toDataURL(this.link, (err, url) => {
        this.qr = url;
      });
      let clipboard = new Clipboard(".clip-button");
      clipboard.on("success", (e) => {
        e.clearSelection();
        Toast(this.$t('复制成功'));
      });
      clipboard.on("error", () => {
        Toast(this.$t('复制失败'));
      });
    },
  },
  watch: {
    account(val) {
      if (val) {
        this.initContract();
      }
    },
  },
};
</script>
<style scoped>
.share {
  width: 100%;
  min-height: 100vh;
  background: #000;
  background: url(../assets/img/fenxiangye_beijing@2x.png) no-repeat;
  background-size: 100% 100%;
}
.logo_box {
  width: 100%;
  height: 6rem;
  text-align: center;
  padding-top: 2rem;
  font-size: 0.64rem;
  color: #fffeee;
}
.logo_box img {
  width: 1.6rem;
  height: 1.6rem;
}
.qrCode {
  text-align: center;
}
.qrCode img {
  width: 3.2rem;
  height: 3.2rem;
  border: .1rem solid #D49C51;
  border-radius: .1rem;
}
.qrCode p {
  font-size: 0.32rem;
  color: #fff;
  margin: 0.3rem 0;
}
.qrCode a {
  font-size: 0.32rem;
  color: #666;
  margin-top: 0.3rem;
  word-break: break-all;
  padding: 0 1rem;
}
.qrCode div {
  width: 2.4rem;
  height: 0.5rem;
  border-radius: 0.1rem;
  text-align: center;
  line-height: 0.5rem;
  color: #fff;
  background: #d49c51;
  margin: 0.3rem auto;
}
.site {
  text-align: center;
  font-size: 0.26rem;
  font-family: PingFang SC;
  font-weight: 400;
  color: #d49c51;
}
.site img{
  width: .4rem;
  margin-bottom: .05rem;
  margin-left: .1rem;
}
</style>
