<template>
  <div class="index">
    <showHeader></showHeader>
    <div class="data_box">
      <div class="data_item">
        <p>{{ $t("算力") }}</p>
        <h2>{{ $t("我的算力") }}：{{balance}}</h2>
        <div class="data_btn">{{ $t("获取") }}</div>
      </div>
      <div class="data_item">
        <p>YFI6 EARNED</p>
        <h2>{{reward}} YFI6</h2>
        <h2>≈ ${{rewardPrice}}</h2>
        <div class="data_btn" @click="getReward">{{ $t("领取") }}</div>
      </div>
    </div>
    <div class="price">
      <p>YFI9{{ $t("价格") }}</p>
      <p>≈<span>${{price}}</span></p>
    </div>
    <div class="goods_list">
      <div class="goods_item" v-for="item in list">
        <div class="item_one">
          <div class="item_logo">
            <img :src="$tokenLogo(item.mainToken)" alt="" />
            <img v-if="item.deputyTokenSympol" :src="$tokenLogo(item.deputyToken)" alt="" />
          </div>
          <div class="cheng">1X</div>
        </div>
        <div class="item_two">
          <div class="item_two_l">{{ $t("质押") }}</div>
          <div class="item_two_r">{{item.mainTokenSympol == 'WBNB' ? 'BNB': item.mainTokenSympol}}<span v-if="item.deputyTokenSympol">·{{item.deputyTokenSympol}}</span></div>
        </div>
        <div class="item_two">
          <div class="item_two_l">{{ $t("分红") }}</div>
          <div class="item_two_r">YFI6</div>
        </div>
        <div class="item_two">
          <div class="item_two_l">APR({{ $t("最高") }})</div>
          <div class="item_two_r">{{Number(item.apr / 100).toFixed(2)}}%</div>
        </div>
        <div class="item_three">
          <div class="item_three_l" @click="stake(item.id)">
            {{ $t("质押") }}
          </div>
          <div class="item_three_r" @click="redeem(item.id)">{{ $t("赎回") }}</div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <!-- <p><b>{{$t('摧毁了数量')}}</b><span>000000</span>YFI9</p> -->
      <p>
        <b>{{ $t("整个") }}</b
        ><span>{{totalPower}}</span>
      </p>
    </div>
    <div class="pop" v-if="isPop">
      <div class="pop_box">
        <div class="pop_title">Account Activation</div>
        <input type="text" v-model="intro" :placeholder="$t('请输入')" />
        <div class="pop_hint">
          {{ $t("链上的激活操作需要确认请不要重复提交") }}
        </div>
        <div class="pop_btn">
          <div class="pop_btn_item" @click="isPop = !isPop">
            {{ $t("取消") }}
          </div>
          <div class="pop_btn_item" @click="active">{{ $t("确认") }}</div>
        </div>
      </div>
    </div>
     <Redeem v-if="showRedeem" :record="record" @hide="showRedeem = false" />
    <!-- <div class="Dialog" v-if="isDialog">
      <div class="Dialog_box">
        <div class="Dialog_title">
          <img src="../assets/img/close.png" @click="isDialog = !isDialog" width="20" alt="" />
        </div>
        <div class="Dialog_content">
          <div class="Dialog_tabs">
            <div :class="dialogTabs == 1 ? 'tabs_active' : ''" @click="dialogTabs = 1">移动</div>
            <div :class="dialogTabs == 2 ? 'tabs_active' : ''" @click="dialogTabs = 2">二维码</div>
          </div>
          <div class="Dialog_content_one" v-if="dialogTabs == 1">
            <p>连接至移动端钱包</p>
            <div class="Dialog_one_btn">链接</div>
          </div>
          <div class="Dialog_content_one" v-if="dialogTabs == 2">
            <p>使用兼容WalletConnectd的钱包扫描二维码</p>
            <p>
              <img src="../assets/img/erwema.png" alt="" />
            </p>
            <p>复制到剪贴板</p>
          </div>
        </div>
      </div>
    </div> -->
  </div>
</template>

<script>
// @ is an alias to /src
import Redeem from '@/components/Redeem'
import showHeader from "@/components/header.vue";
import investAbi from '@/utils/investAbi'
import erc20Abi from '@/utils/erc20Abi'
import routerPathAbi from '@/utils/routerPathAbi'
import config from '@/utils/config'
import { mapState } from 'vuex'
import BigNumber from 'bignumber.js'
import { intro } from '@/storage'
import { Toast } from 'vant';
export default {
  name: "Home",
  data() {
    return {
      isPop: false,
      isDialog: false,
      dialogTabs: 2,
      investContract: null,
      yfi6Contract: null,
      routerPathContract: null,
      usdtContract: null,
      yfi9Contract: null,
      price: 0,
      balance: 0,
      reward: 0,
      totalPower: 0,
      rewardPrice: 0,
      intro: intro.get() || '',
      list: [
        {
          0: "1",
          1: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
          2: "0x261828B6e70bf05136DA9D0c8BC0FffdEeCF0aE3",
          3: "WBNB",
          4: "YFI9",
          apr: "0",
          deputyToken: "0x261828B6e70bf05136DA9D0c8BC0FffdEeCF0aE3",
          deputyTokenSympol: "YFI9",
          id: "1",
          mainToken: "0xbb4CdB9CBd36B01bD1cBaEBF2De08d9173bc095c",
          mainTokenSympol: "WBNB"
        },
        {
          0: "2",
          1: "0x55d398326f99059fF775485246999027B3197955",
          2: "0x261828B6e70bf05136DA9D0c8BC0FffdEeCF0aE3",
          3: "USDT",
          4: "YFI9",
          apr: "0",
          deputyToken: "0x261828B6e70bf05136DA9D0c8BC0FffdEeCF0aE3",
          deputyTokenSympol: "YFI9",
          id: "2",
          mainToken: "0x55d398326f99059fF775485246999027B3197955",
          mainTokenSympol: "USDT"
        },
        {
          0: "3",
          1: "0x261828B6e70bf05136DA9D0c8BC0FffdEeCF0aE3",
          2: "0x0000000000000000000000000000000000000000",
          3: "YFI9",
          4: "",
          apr: "0",
          deputyToken: "0x0000000000000000000000000000000000000000",
          deputyTokenSympol: "",
          id: "3",
          mainToken: "0x261828B6e70bf05136DA9D0c8BC0FffdEeCF0aE3",
          mainTokenSympol: "YFI9"
        }
      ],
      record: {
        id: 0,
        mainAmount: 0,
        deputyAmount: 0,
        mainTokenSympol: '--',
        deputyTokenSympol: '--',
        power: 0
      },
      showRedeem: false
    };
  },
   computed: {
    ...mapState(['account', 'provider'])
  },
  components: {
    showHeader,
    Redeem
  },
  created () {
    if(this.provider) {
       this.initContract() 
    }
    if(this.account) {
      this.getData()
    }
  },
  methods: {
    initContract () {
      this.investContract = new this.provider.eth.Contract(investAbi, config.investAddress);
      this.yfi6Contract = new this.provider.eth.Contract(erc20Abi, config.yfi6Address);
      this.yfi9Contract = new this.provider.eth.Contract(erc20Abi, config.yfi9Address);
      this.usdtContract = new this.provider.eth.Contract(erc20Abi, config.usdtAddress);
      this.routerPathContract = new this.provider.eth.Contract(routerPathAbi, config.routerPathAddress);
      this.initData()
    },
    initData () {
      this.getPrice()
      this.getList()
      this.getTotalPower()
    },
    async getPrice () {
      let decimals = await this.yfi9Contract.methods.decimals().call()
      let price = await this.routerPathContract.methods.getBestPathAmount(config.yfi9Address, config.usdtAddress, 1 + '0'.repeat(decimals)).call()
      this.price = new BigNumber(price).dividedBy(10**decimals).toFixed(4)
    },
    async getData () {
      let rewardPrice = 0;
      let decimals = await this.yfi6Contract.methods.decimals().call()
      let balance =  await this.investContract.methods.balanceOf(this.account).call()
      let reward =  await this.investContract.methods.earned(this.account).call()
      if(Number(reward) > 0) {
        rewardPrice = reward / 2;
      }  
      this.reward = new BigNumber(reward).dividedBy(10**decimals).toFixed(4)
      this.balance = new BigNumber(balance).dividedBy(10**18).toFixed(4)
    
      this.rewardPrice = new BigNumber(rewardPrice).dividedBy(10**decimals).toFixed(4)
    },
    async getTotalPower () {
      let totalPower = await this.investContract.methods.totalSupply().call()
      this.totalPower = new BigNumber(totalPower).dividedBy(10**18).toFixed(4)
    },
    async getList () {
      let arr = []
      let list = await this.investContract.methods.getAllProductStruct().call()
      for(let i=0; i < list.length;i++) {
        let apr = await this.investContract.methods.getApr(list[i].mainToken, 1 + '0'.repeat(18), '1000000000000000000').call()
        let params = {...list[i], apr: apr}
        arr.push(params)
      }
      this.list = arr
      console.log(arr)
      console.log(this.list)
    },
    async stake (id) {
      if(!this.provider) {
        Toast(this.$t('请在dapp浏览器里打开'))
        return
      }
      let result = await this.investContract.methods.isActive(this.account).call()
      if(!result) {
        this.isPop = true
        return
      }
      this.$router.push({name: 'Stake', params: {id: id}})
    },
    async active () {
      if(!this.intro) {
        Toast(this.$t('请先输入推荐人地址'))
        return
      }
      let result = await this.investContract.methods.bindIntro(this.intro).send({from: this.account})
      Toast(this.$t('激活成功'))
      this.isPop = false
      this.isActive = true
    },
    async getReward () {
      let result = await this.investContract.methods.getReward().send({from: this.account})
      Toast(this.$t('领取成功'))
      this.getData()
    },
    async redeem (id) {
      if(!this.provider) {
        Toast(this.$t('请在dapp浏览器里打开'))
        return
      }
      let result = await this.investContract.methods.isActive(this.account).call()
      if(!result) {
        this.isPop = true
        return
      }
      let record = await this.investContract.methods.getRecord(id).call({from: this.account})
      console.log(record)
      if(Number(record.power) <= 0) {
        Toast(this.$t('暂未质押'))
        return
      }
      let product = await this.investContract.methods.getProduct(record.stakeType).call()
      let mainToken = new this.provider.eth.Contract(erc20Abi, product.mainToken);
      let mainDecimals = await mainToken.methods.decimals().call()
      let deputyToken = null
      let deputy = 0
      let main = new BigNumber(record.mainTokenAmount).dividedBy(10 ** mainDecimals).toFixed(4)
      if (product.deputyTokenSympol) {
        deputyToken = new this.provider.eth.Contract(erc20Abi, product.deputyToken);
        let decimals = await deputyToken.methods.decimals().call()
        deputy = new BigNumber(record.deputyTokenAmount).dividedBy(10 ** decimals).toFixed(4)
      }
      console.log(product)
      let params = {
        id: id,
        mainAmount: main,
        deputyAmount: deputy,
        mainTokenSympol: product.mainTokenSympol == 'WBNB' ? 'BNB' : product.mainTokenSympol,
        deputyTokenSympol: product.deputyTokenSympol,
        power: new BigNumber(record.power).dividedBy(10 ** 18).toFixed(4)
      }
      this.record = params
      this.showRedeem = true
      // Toast('领取成功')
      // this.getData()
    }
  },
  watch: {
    account (val) {
      if(val) {
        this.getData()
      }
    },
    provider (val) {
      if(val) {
        this.initContract()
      }
    }
  }
};
</script>
