import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    account: '',
    provider: null,
    currentBalance: ""
  },
  mutations: {
    updateAccount(state, value) {
      state.account = value;
    },
    updateProvider(state, value) {
      state.provider = value;
    },
    updateCurrentBalance(state, value) {
      state.currentBalance = value;
    }
  },
  actions: {
  },
  modules: {
  }
})
