<template>
  <div class="header">
    <div class="header_box">
      <div class="" @click="asile = !asile">
        <img src="../assets/img/caidan.png" class="cebian" alt="" />
      </div>
      <div>
        <img class="title_img" src="../assets/img/1111.png"  alt="">
      </div>
      <div class="language" @click="langeuage = !langeuage">
        <p v-if="langeuageType == 'en'">{{ $t("English") }}</p>
        <p v-if="langeuageType == 'cn'">{{ $t("简体中文") }}</p>
        <p v-if="langeuageType == 'tc'">{{ $t("繁体中文") }}</p>
        <!-- <img src="../assets/img/yingguo.png" v-if="langeuageType == 'en'" alt="" />
        <img src="../assets/img/zhongguo.png" v-if="langeuageType == 'cn'" alt="" />
        <img src="../assets/img/XIANG.png" v-if="langeuageType == 'tc'" alt="" /> -->
        <div class="language_Box" v-if="langeuage">
          <p @click="changeLanguage('cn')">{{ $t("简体中文") }}</p>
          <p @click="changeLanguage('tc')">{{ $t("繁体中文") }}</p>
          <p @click="changeLanguage('en')">{{ $t("English") }}</p>
        </div>
      </div>
    </div>
    <div :class="asile ? 'asile' : 'asileNo'" @click.stop="asile = !asile">
      <div :class="asile ? 'menu' : 'menuNo'">
        <div class="menu_one">
          <div class="menu_one_title over">{{ $t("地址") }}</div>
          <div class="copy_btn">
            {{ account }}
            <img
              src="../assets/img/fuzhi.png"
              :data-clipboard-text="account"
              class="clip-button"
              alt=""
            />
          </div>
          <div class="status_header" v-if="account">
            {{ $t("状态") }}{{$t('已连接')}}
            <img src="../assets/img/hint.png" alt="" @click.stop="isHint = !isHint">
            <div class="hint_text" v-if="isHint">
              <p>1.{{$t('请确保钱包是最新版本')}}</p>
              <p>2.{{$t('请确认当前钱包为BSC钱包')}}</p>
              <p>3.{{$t('请刷新DAPP')}}</p>
            </div>
          </div>
          <div class="status_header"  v-else>
            <span @click="isPop = true">{{ $t("状态") }}{{$t('未连接')}}</span>
            <img src="../assets/img/hint.png" alt="" @click.stop="isHint = !isHint">
            <div class="hint_text" v-if="isHint">
              <p>1.{{$t('请确保钱包是最新版本')}}</p>
              <p>2.{{$t('请确认当前钱包为BSC钱包')}}</p>
              <p>3.{{$t('请刷新DAPP')}}</p>
            </div>
          </div>
        </div>
        <div class="menu_two">
          <div :class="isNav == 10 ? 'menu_active' : 'menu_two_item'" style="padding-right: 10px">
            <div class="" @click="$router.push('/personal')">
              <img src="../assets/img/gerenzhongxin.png" alt="" />
              {{ $t("个人算力") }}
            </div>
            <div class="">{{ Number(balance - introBalance).toFixed(4) }}</div>
          </div>
          <div :class="isNav == 11 ? 'menu_active' : 'menu_two_item'" style="padding-right: 10px">
            <div class=""  @click="$router.push('/team')">
              <img src="../assets/img/tuandui.png" alt="" />
              {{ $t("团队算力") }}
            </div>
            <div class="">{{ introBalance }}</div>
          </div>
        </div>
        <div class="menu_two">
          <div
            :class="isNav == 0 ? 'menu_active' : 'menu_two_item'"
            @click="changeNav(1)"
          >
            <div class="">
              <img v-if="isNav == 0" src="../assets/img/tubiao-S.png" alt="" />
              <img v-else src="../assets/img/tubiao-.png" alt="" />
              {{ $t("首页") }}
            </div>
            <div class="">
              <img src="../assets/img/right.png" class="rigth_img" alt="" />
            </div>
          </div>
          <div
            :class="isNav == 1 ? 'menu_active' : 'menu_two_item'"
            @click="changeNav(2)"
          >
            <div class="">
              <img
                v-if="isNav == 1"
                src="../assets/img/icon-test_s.png"
                alt=""
              />
              <img v-else src="../assets/img/icon-test.png" alt="" />
              {{ $t("邀请分享") }}
            </div>
            <div class="">
              <img src="../assets/img/right.png" class="rigth_img" alt="" />
            </div>
          </div>
          <div
            :class="isNav == 2 ? 'menu_active' : 'menu_two_item'"
            @click="changeNav(3)"
          >
            <div class="">
              <img v-if="isNav == 2" src="../assets/img/gonggao_s.png" alt="" />
              <img v-else src="../assets/img/gonggao.png" alt="" />
              {{ $t("系统公告") }}
            </div>
            <div class="">
              <img src="../assets/img/right.png" class="rigth_img" alt="" />
            </div>
          </div>
          <div
            :class="isNav == 3 ? 'menu_active' : 'menu_two_item'"
            @click="changeNav(4)"
          >
            <div class="">
              <img v-if="isNav == 3" src="../assets/img/cloud_s.png" alt="" />
              <img v-else src="../assets/img/cloud.png" alt="" />
              {{ $t("开源源码") }}
            </div>
            <div class="">
              <img src="../assets/img/right.png" class="rigth_img" alt="" />
            </div>
          </div>
          <div
            :class="isNav == 4 ? 'menu_active' : 'menu_two_item'"
            @click="changeNav(5)"
          >
            <div class="">
              <img v-if="isNav == 4" src="../assets/img/qianbi_s.png" alt="" />
              <img v-else src="../assets/img/qianbi.png" alt="" />
              {{ $t("NFT交易") }}
            </div>
            <div class="">
              <img src="../assets/img/right.png" class="rigth_img" alt="" />
            </div>
          </div>
          <div
            :class="isNav == 5 ? 'menu_active' : 'menu_two_item'"
            @click="changeNav(6)"
          >
            <div class="">
              <img v-if="isNav == 5" src="../assets/img/qian_s.png" alt="" />
              <img v-else src="../assets/img/qian.png" alt="" />
              {{ $t("质押借贷") }}
            </div>
            <div class="">
              <img src="../assets/img/right.png" class="rigth_img" alt="" />
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="pop" v-if="isPop">
      <div class="pop_box">
        <div class="pop_title">Account Activation</div>
        <input type="text" v-model="intro" :placeholder="$t('请输入')" />
        <div class="pop_hint">
          {{ $t("链上的激活操作需要确认请不要重复提交") }}
        </div>
        <div class="pop_btn">
          <div class="pop_btn_item" @click="isPop = !isPop">
            {{ $t("取消") }}
          </div>
          <div class="pop_btn_item" @click="active">{{ $t("确认") }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import investAbi from "@/utils/investAbi";
import erc20Abi from "@/utils/erc20Abi";
import BigNumber from "bignumber.js";
import config from "@/utils/config";
import { intro } from "@/storage";
import { Toast } from "vant";
import Clipboard from "clipboard";
export default {
  data() {
    return {
      langeuage: false,
      asile: false,
      isNav: 1,
      isActive: false,
      investContract: null,
      yfi6Contract: null,
      intro: intro.get() || "",
      isPop: false,
      balance: 0,
      introBalance: 0,
      isNav: 0,
      isHint:false,
      langeuageType:'cn'
    };
  },
  mounted() {
    this.isNav = this.$route.meta.isNav;
  },
  computed: {
    ...mapState(["account", "provider"]),
  },
  created() {
    if (this.account) {
      this.initContract();
    }
    this.langeuageType = sessionStorage.getItem("languageSet") || 'cn';
  },
  methods: {
    changeLanguage(type) {
      this.langeuageType = type
      sessionStorage.setItem("languageSet", type);
      location.reload();
    },
    changeNav(type) {
      if (type == 1) {
        this.$router.push("/");
      } else if (type == 2) {
        this.$router.push("/share");
      } else if (type == 3) {
        this.$router.push("/notice");
      } else {
        Toast(this.$t('即将推出'));
      }
    },
    initData() {
      this.getIsActive();
      this.getData();
    },
    initContract() {
      let clipboard = new Clipboard(".clip-button");
      clipboard.on("success", (e) => {
        e.clearSelection();
        Toast(this.$t('复制成功'));
      });
      clipboard.on("error", () => {
        Toast(this.$t('复制失败'));
      });
      this.investContract = new this.provider.eth.Contract(
        investAbi,
        config.investAddress
      );
      // this.yfi6Contract = new this.provider.eth.Contract(erc20Abi, config.yfi6Address);
      this.initData();
    },
    async getData() {
      let balance = await this.investContract.methods
        .balanceOf(this.account)
        .call();
      this.balance = new BigNumber(balance).dividedBy(10 ** 18).toFixed(4);
      let introPower = await this.investContract.methods
        .introPower(this.account)
        .call();
      this.introBalance = new BigNumber(introPower)
        .dividedBy(10 ** 18)
        .toFixed(4);
    },
    async getIsActive() {
      let result = await this.investContract.methods
        .isActive(this.account)
        .call();
      this.isActive = result;
    },
    async active() {
      if (!this.intro) {
        Toast(this.$t('请先输入推荐人地址'));
        return;
      }
      let result = await this.investContract.methods
        .bindIntro(this.intro)
        .send({ from: this.account });
      Toast(this.$t('激活成功'));
      this.isPop = false;
      this.isActive = true;
    },
  },
  watch: {
    account(val) {
      if (val) {
        this.initContract();
      }
    },
    $route() {
      this.isNav = this.$route.meta.isNav;
    },
  },
};
</script>

<style>
.title_img{
  height: 1rem;
}
div {
  box-sizing: border-box;
}
.header_box {
  width: 100%;
  height: 0.96rem;
  background: #fff;
  color: #333;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 0.3rem;
  font-size: 0.36rem;
  font-weight: bold;
}
.header_box > div{
  text-align: center;
  width: 33%;
}
.header_box > div:nth-child(1){
  text-align: left;
}
.header_box > div:nth-child(3){
  text-align: right;
}
.header .logo {
  width: 1rem;
  height: 0.64rem;
}
.cebian {
  width: 0.64rem !important;
  height: 0.64rem;
}
.language {
  position: relative;
  font-size: 0.28rem;
}
.language_Box {
  position: absolute;
  top: 0.8rem;
  right: 0;
  min-width: 1.64rem;
  background: #fff;
  font-size: 0.28rem;
  line-height: 0.6rem;
  text-align: center;
}
.asile {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
  transition: all 0.5s ease;
}
.asileNo {
  height: 0;
  width: 0;
  overflow: hidden;
  opacity: 0;
}
.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 5.12rem;
  height: 100vh;
  background: #fff;
  padding: 0.15rem;
  transition: all 0.5s ease;
}
.menu_one {
  width: 100%;
  background: #fabf18;
  border-radius: 0.15rem;
}
.menu_one_title {
  font-size: 0.48rem;
  color: #fff;
  padding: 0.16rem 0.32rem;
}
.menu_one_title span {
  font-size: 0.26rem;
}
.copy_btn {
  padding: 0.16rem 0.32rem;
  font-size: 0.24rem;
  color: #fff;
  word-wrap: break-word;
  word-break: normal;
  width: 80%;
}
.copy_btn img {
  width: 0.2rem;
  height: 0.2rem;
}
.status_header {
  padding: 0.16rem 0.32rem;
  font-size: 0.26rem;
  color: #fff;
}
.status_header img{
  width: .3rem;
  height: .3rem;
  margin-bottom: .05rem;
  margin-left: .2rem;
}
.menu_two {
  margin-top: 0.2rem;
  font-size: 0.24rem;
  color: #333;
  margin-bottom: 1rem;
}
.menu_two img {
  width: 0.3rem;
  height: 0.3rem;
  margin-right: 0.2rem;
}
.menu_two_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.28rem;
  padding: 0 0.1rem 0 0.2rem;
}
.menu_two_item div {
  display: flex;
  align-items: center;
}
.rigth_img {
  width: 0.3rem !important;
  height: 0.3rem !important;
}
.menu_active {
  color: #fabf18;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  font-size: 0.28rem;
  padding: 0 0.1rem 0 0.2rem;
}
.pop,
.Dialog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.4);
}
.pop_box {
  width: 80%;
  min-height: 2rem;
  background: #fabf18;
  margin: 50% auto;
  padding: 0.3rem;
}
.pop_title {
  text-align: center;
  font-size: 0.32rem;
  color: #fff;
}
.pop_box input {
  width: 100%;
  height: 0.8rem;
  padding-left: 0.2rem;
  background: #fff;
  margin-top: 0.2rem;
  border-radius: 0.1rem;
}
.pop_hint {
  font-size: 0.24rem;
  color: #fff;
  margin-top: 0.2rem;
}
.pop_btn {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-top: 0.2rem;
}
.pop_btn_item {
  border: 0.01rem solid #fff;
  line-height: 0.5rem;
  padding: 0 0.3rem;
  color: #fff;
  border-radius: 0.1rem;
  font-size: 0.24rem;
}
</style>