<template>
  <div class="personal">
    <showHeader></showHeader>
    <div class="personal_box">
        <div class="personal_title">
            {{$t('个人算力')}}
        </div>
        <div class="personal_item">
            <div>{{$t('基础算力')}}</div>
            <div>{{balance}}</div>
        </div>
        <div class="personal_item">
            <div>{{$t('团队算力')}}</div>
            <div>{{introBalance}}</div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import showHeader from "@/components/header.vue";
import investAbi from "@/utils/investAbi";
import BigNumber from "bignumber.js";
import config from "@/utils/config";
export default {
  data() {
    return {
      investContract: null,
      balance: 0,
      introBalance: 0
    };
  },
   components: {
    showHeader,
  },
  computed: {
    ...mapState(["account", "provider"]),
  },
  created() {
    if (this.account) {
      this.initContract();
    }
  },
  methods: {
    initData() {
      this.getData();
    },
    initContract() {
      this.investContract = new this.provider.eth.Contract(
        investAbi,
        config.investAddress
      );
      // this.yfi6Contract = new this.provider.eth.Contract(erc20Abi, config.yfi6Address);
      this.initData();
    },
    async getData() {
      let balance = await this.investContract.methods
        .balanceOf(this.account)
        .call();
      this.balance = new BigNumber(balance).dividedBy(10 ** 18).toFixed(4);
      let introPower = await this.investContract.methods
        .introPower(this.account)
        .call();
      this.introBalance = new BigNumber(introPower)
        .dividedBy(10 ** 18)
        .toFixed(4);
    }
  },
  watch: {
    account(val) {
      if (val) {
        this.initContract();
      }
    },
    $route() {
      this.isNav = this.$route.meta.isNav;
    },
  },
};
</script>

<style scoped>
    .personal_box{
        width: 6.9rem;
        margin: .2rem auto;
        background: #fff;
        border-radius: .2rem;
        padding: .2rem;
    }
    .personal_title{
        height: .8rem;
        line-height: .8rem;
        border-bottom: .01rem solid #eee;
        margin-bottom: .2rem;
        color: #fabf18;
    }
    .personal_item{
        display: flex;
        align-items: center;
        justify-content: space-between;
        line-height: .6rem;
    }
</style>
