import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueI18n from 'vue-i18n'
import { Toast, Popup, Popover, Slider, Picker, List } from 'vant'
import 'vant/lib/toast/style'
import 'vant/lib/popup/style'
import 'vant/lib/popover/style'
import 'vant/lib/slider/style'
import 'vant/lib/picker/style'
import 'vant/lib/list/style'
import { intro } from "./storage"
Vue.use(VueI18n);
Vue.use(List)
Vue.use(Picker)
Vue.use(Slider)
Vue.use(Popover)
Vue.use(Popup)
Vue.use(Toast)
Vue.config.productionTip = false

Vue.prototype.$tokenLogo = (address) => {
  const path = `https://xuruiju.gitee.io/assets/blockchains/smartchain/assets/${address}/logo.png`
  return path
}
const i18n = new VueI18n({
  locale: sessionStorage.getItem('languageSet') || 'cn', // 将要切换的语言，可以通过url拼的参数获取，用户行为select选择获取，本地manifest配置获取等，根据场景动态获取
  messages: {
    'cn': require('./language/cn'),
    'en': require('./language/en'),
    'tc': require('./language/tc')
  }
});
let queryParse = (str) => {
  if (!str || str === '0') {
    return {}
  }
  let dataArr = decodeURIComponent(str).split('&')
  let params = {}
  dataArr.forEach(query => {
    let queryItem = query.split('=')
    if (queryItem.length === 1) {
      params.id = queryItem[0]
    } else {
      params[queryItem[0]] = queryItem[1]
    }
  })
  return params
}

let href = window.location.href.replace(/#/g, '').replace(/\?utm_source=tokenpocket/g, '')
if (href.includes('?')) {
  let search = href.slice(href.indexOf('?') + 1, href.length)
  let params = queryParse(search)
  if (params.intro) {
    // 是否有推荐邀请码
    intro.set(params.intro)
  }
}
new Vue({
  router,
  store,
  i18n,
  render: function (h) { return h(App) }
}).$mount('#app')
